.FsCISearch {
    border: 1px solid var(--fashion-head);
}

.FsCISearch_fetching {
    height: 330px;

    .FetchingDiv_item {
        animation-name: fetching_div;
    }
}

.FsCISearch_item {
    width: 190px;
    padding: 4px;
}

//
@keyframes fetching_div {
    0% {
        background-color: var(--fashion-head);
    }
    50% {
        background-color: var(--md-bg-ccc);
    }
    100% {
        background-color: transparent;
    }
}
